import styled from 'styled-components';

import { MEDIA } from '../../constants/style.constant';

const StyledCheckInList = styled.div`
    @media (max-width: 1100px) {
        padding: 0 35px;
    }
    @media (max-width: ${MEDIA.SM_MAX}px) {
        padding: 0 35px;
    }
    @media (max-width: ${MEDIA.XS_MAX}px) {
        padding: 0 10px;
    }

    input {
        border: 1px solid #FCD406;
        padding: 5px;
    }

    .flex {
        display: flex;
    }
    .flex-space-between {
        justify-content: space-between;
    }
    .flex-align-center {
        align-items: center;
    }

    .title {
        text-align: center;
        margin-bottom: 40px;
        padding-top: 20px;
    }

    .menu-bar {
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
        .date-container {
            display: flex;
        }
        .date-picker:first-child {
            margin-right: 25px;
        }
        @media (max-width: ${MEDIA.SM_MAX}px) {
            display: inherit;
            .date-picker:first-child {
                margin-right: 0px;
            }
            .date-container {
                display: inherit;
            }
            .date-picker {
                margin-bottom: 15px;
            }
            .date-picker input {
                width: 100%;
            }
            .search-btn {
                width: 100%;
            }
        }

    }

    .section {
        margin-bottom: 40px;
    }

    .table .ot-salary-total td {
        border-top: 5px solid #ededed;
        font-size: 18px;
        @media (max-width: ${MEDIA.SM_MAX}px) {
            border: none;
            &:first-child, &:last-child {
                display: none !important;
            }
        }
    }

    .table .ot-salary-total {
        @media (max-width: ${MEDIA.SM_MAX}px) {
            border: 3px solid #FCD406;
        }
    }

    .table .ot-salary-hint {
        display: none;

        &.re-calculate {
            display: inherit;
            color: red;
            font-size: 16px;
            font-weight: 400;
        }
    }

    .radio-button {
        background-color: #F0F0F0;
        margin-right: 10px;
        &:hover {
            @media (min-width: 1100px) {
                background-color: #FCD406;
            }
        }
        &:hover, & *:hover {
            cursor: pointer;
        }
        input {
            display: none;
        }
        label {
            margin: 0;
            padding: 5px 10px;
        }
    }
    .radio-button.checked {
        background-color: #FCD406;
    }

    .setting-section {
        padding: 20px;
        background-color: white;

        .radio-button label {
            width: 100%;
            height: 100%;
        }

        .salary-section {
            display: flex;
            align-items: center;

            .salary-input-group {
                display: flex;
                align-items: center;
            }
            .salary-input input{
                width: calc(100% - 30px);
                text-align: right;
            }
            .hourly-wage {
                text-align: right;
            }
            .money-unit {
                margin-left: 5px;
            }
        }

        @media (max-width: ${MEDIA.SM_MAX}px) {
            .salary-section {
                display: inherit;
                > label {
                    display: block;
                }
                .salary-input-group {
                    display: inherit;
                    .radio-button {
                        text-align: center;
                        margin-bottom: 5px;
                    }
                }
                .salary-input {
                    margin-bottom: 15px;
                }
            }
        }
    }

    .day-off-section {
        .day-off-group {
            display: flex;
            align-items: center;
        }

        @media (max-width: ${MEDIA.SM_MAX}px) {
            .day-off-group {
                display: inherit;
            }
            .day-off-group > label {
                display: block;
            }
            .day-off-group .radio-button {
                margin-bottom: 5px;
                text-align: center;
            }
        }
    }

    .calc-container {
        display: flex;
        justify-content: flex-end;
        button {
            padding-top: 10px;
            padding-bottom: 10px;
        }
        margin-top: 15px;

        @media (max-width: ${MEDIA.SM_MAX}px) {
            display: block;
            button {
                width: 100%;
            }
        }
    }
`;

export default StyledCheckInList;


